@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&family=Roboto&display=swap);
/*  title:  resets
    notes:  application global resets/standardisation
 ==================================================================================================================== */




/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
===================================================================================================================== */
* {
    box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline;
}

/*  title:  theming
    notes:  set global variables available for all atomic components
 ==================================================================================================================== */



/*  responsive breakpoints
 ==================================================================================================================== */
:root {
    --global-break-small: 440px;
    --global-break-medium: 767px;
    --global-break-large: 1440px;
}




/*  branding colours
 ==================================================================================================================== */
:root {
    --Global-color-1-darker: 28, 0, 202;
    --Global-color-2-darker: 5, 195, 150;
    --Global-color-3-darker: 195, 74, 6;
    --Global-color-4-darker: 207, 0, 87;
    --Global-color-5-darker: 66, 78, 121;

    --Global-color-1: 70, 50, 251;      /*  Blue       */
    --Global-color-2: 40, 216, 173;     /*  Green      */
    --Global-color-3: 250, 143, 45;     /*  Orange     */
    --Global-color-4: 235, 29, 116;     /*  Pink       */
    --Global-color-5: 114, 123, 155;    /*  Greyish    */

    --Global-color-error: 207, 0, 87;
    --Global-color-success: 5, 195, 150;

    --Global-border-color: 170, 176, 195;
    --Global-border-radius: 5px;
}




/*  typography
 ==================================================================================================================== */

:root {
    --Global-base-font-color: 66, 78, 121;
    --Global-base-font-family: 'Nunito', sans-serif;
    --Global-base-font-padding: .75rem 0;
    --Global-base-font-size: 16px;
    --Global-base-font-weight: 400;

    --Global-heading-color: 32, 43, 55;
    --Global-heading-font-family: 'Roboto', sans-serif;
    --Global-heading-font-weight: 700;

    --Global-h1-size: 1.25rem;
    --Global-h2-size: 1.0625rem;
    --Global-h3-size: 1rem;
    --Global-h4-size: .875rem;
}




/*  buttons
 ==================================================================================================================== */
:root {
    --Global-button-color-1-darker: var(--Global-color-1-darker);
    --Global-button-color-2-darker: var(--Global-color-2-darker);

    --Global-button-color-1: var(--Global-color-1);
    --Global-button-color-2: var(--Global-color-2);

    --Global-button-border-radius: var(--Global-border-radius, 5px);

    --Global-button-padding: .875rem 1.5rem;
}




/*  forms
 ==================================================================================================================== */
:root {
    --Global-form-border-color: var(--Global-border-color);
    --Global-form-border-radius: var(--Global-border-radius, 5px);
}


