/*  title:  button
    notes:  atom styles
 ==================================================================================================================== */




/*  variables
===================================================================================================================== */
:root {
    --Button-border-radius: var(--Global-button-border-radius, 5px);
    --Button-color-1: var(--Global-button-color-1, 0 0 255);
    --Button-color-2: var(--Global-button-color-2, 0 255 0);
    --Button-color-1-darker: var(--Global-button-color-1-darker, 0 0 220);
    --Button-color-2-darker: var(--Global-button-color-2-darker, 0 220 0);
    --Button-font-size: 1rem;
    --Button-padding: var(--Global-button-padding, 12px 24px);
}


/* base
===================================================================================================================== */
.Button {
    border-radius: 5px;
    border-radius: var(--Button-border-radius);
    font-size: 1rem;
    font-size: var(--Button-font-size);
    font-weight: 700;
    line-height: 1;
    padding: 12px 24px;
    padding: var(--Button-padding);
    transition: .3s;
}

    /*  test states
    ================================================================================================================= */
    .Button:not(:disabled):hover,
    .Button:focus,
    .Button:focus-visible,
    .Button:focus-within,
    .Button:active {
        background: none;
        cursor: pointer;
        transition: .2s;
    }





    /*  test
    ================================================================================================================= */
    .Button {
        background-color: rgb(0 0 255);
        background-color: rgb(var(--Button-color-1));
        border: none;       /* only add to outline */
        color: #fff;    /* TODO might have to revisit this, would normally run a contrast function on the background */
    }
        /*  test: states
        ============================================================================================================= */
        .Button:not(:disabled):hover,
        .Button:not(.outline):focus,
        .Button:not(.outline):focus-visible,
        .Button:not(.outline):focus-within,
        .Button:not(.outline):active {
            background-color: rgba(0 0 220);
            background-color: rgba(var(--Button-color-1-darker));
        }
        .Button:disabled {
            background-color: rgba(0 0 255, .3);
            background-color: rgba(var(--Button-color-1), .3);
        }

        /*  test: outline
        ============================================================================================================= */
        .Button.outline {
            background-color: transparent;
            border: 1px solid rgb(0 0 255);
            border: 1px solid rgb(var(--Button-color-1));
            color: rgb(0 0 255);
            color: rgb(var(--Button-color-1));
        }
        .Button:not(:disabled).outline:hover,
        .Button.outline:focus,
        .Button.outline:focus-visible,
        .Button.outline:focus-within,
        .Button.outline:active {
            background-color: transparent;
            border-color: rgb(var(--Global-button-color-1-darker));
            color: rgb(var(--Global-button-color-1-darker));
        }
        .Button.outline:disabled {
            border: 1px solid rgba(0 0 255, .3);
            border: 1px solid rgba(var(--Button-color-1), .3);
            color: rgba(0 0 255, .3);
            color: rgba(var(--Button-color-1), .3);
        }




    /*  secondary
    ================================================================================================================= */
    .Button.secondary {
        background-color: rgb(0 255 0);
        background-color: rgb(var(--Button-color-2));
        border: none;       /* only add to outline */
    }
        /*  secondary: outline
        ============================================================================================================= */
        .Button:not(:disabled).secondary:hover,
        .Button.secondary:not(.outline):focus,
        .Button.secondary:not(.outline):focus-visible,
        .Button.secondary:not(.outline):focus-within,
        .Button.secondary:not(.outline):active {
            background-color: rgb(0 220 0);
            background-color: rgb(var(--Button-color-2-darker));
        }
        .Button.secondary:disabled {
            background-color: rgba(0 255 0, .3);
            background-color: rgba(var(--Button-color-2), .3);
        }

        /*  secondary: outline
        ============================================================================================================= */
        .Button.secondary.outline {
            background-color: transparent;
            border: 1px solid rgb(0 255 0);
            border: 1px solid rgb(var(--Button-color-2));
            color: rgb(0 255 0);
            color: rgb(var(--Button-color-2));
        }
        .Button:not(:disabled).secondary.outline:hover,
        .Button.secondary.outline:focus,
        .Button.secondary.outline:focus-visible,
        .Button.secondary.outline:focus-within,
        .Button.secondary.outline:active {
            background-color: transparent;
            border: 1px solid rgb(0 220 0);
            border: 1px solid rgb(var(--Button-color-2-darker));
            color: rgb(0 220 0);
            color: rgb(var(--Button-color-2-darker));
        }
        .Button.secondary.outline:disabled {
            border: 1px solid rgba(0 255 0, .3);
            border: 1px solid rgba(var(--Button-color-2), .3);
            color: rgba(0 255 0, .3);
            color: rgba(var(--Button-color-2), .3);
        }




    /*  minimal
    ================================================================================================================= */
    .Button.minimal {
        background-color: transparent;
        border: none;
        color: rgba(0 0 255, 1);
        color: rgba(var(--Button-color-1), 1);
    }
    .Button:not(:disabled).minimal:hover,
    .Button.minimal:focus,
    .Button.minimal:focus-visible,
    .Button.minimal:focus-within,
    .Button.minimal:active {
        background-color: transparent;
        border-color: transparent;
        color: #1C00CA;      /*  TODO we might want some pure css to modify global branding colours but beyond the scope here */
    }
    .Button.minimal:disabled {
        color: rgba(0 0 255, .3);
        color: rgba(var(--Button-color-1), .3);
    }




    /*  disabled
    ================================================================================================================= */
    .Button:disabled {
        background-color: rgba(0 0 255, .3);
        background-color: rgba(var(--Button-color-1), .3);
        cursor: not-allowed;
    }


    /*  large
    ================================================================================================================= */
    .Button.large {
        font-size: 22px;
    }


    /*  round
    ================================================================================================================= */
    .Button.round {
        -webkit-align-items: center;
                align-items: center;
        display: -webkit-flex;
        display: flex;
        border-radius: 100%;
        -webkit-justify-content: center;
                justify-content: center;
        padding: 1rem;
    }
/*  title:  label
    notes:  atom styles
 ==================================================================================================================== */




/*  variables
===================================================================================================================== */
:root {
    --Label-color: #202B37;
}


/*  base
===================================================================================================================== */
.Label {
    color: #202B37;
    color: var(--Label-color);
    display: block;
    font-weight: 700;
}
    /*  when used in a form there will be more than 1 "group" of inputs, so will need margin
    ================================================================================================================= */
    form .Label:not(:first-of-type) {
        margin-top: 2rem;
    }

    /*  when followed by some form element, give some margin (NOTE: could potentially do this on the molecule but would require a wrapper class)
    ================================================================================================================= */
    .Label ~ input,
    .Label ~ textarea,
    .Label ~ .Pin,
    .Label ~ select {
        margin-top: 1rem;
    }
/*  title:  formInput
    notes:  atom styles
 ==================================================================================================================== */




/*  variables
===================================================================================================================== */
:root {
    --Form-input-border: 1px solid var(--Form-input-border-color);
    --Form-input-border-color: rgb(var(--Global-form-border-color, 170 176 195));
    --Form-input-border-radius: var(--Global-form-border-radius, 5px);
    --Form-input-font-size: var(--Global-base-font-size, 16px);
    --Form-input-color-error: var(--Global-color-error, 255 0 0);
    --Form-input-color-success: var(--Global-color-success, 0 255 0);
    --Form-input-padding: 0 1rem;
}


/*  base
===================================================================================================================== */
.Form-input {
    border: 1px solid rgb(170 176 195);
    border: var(--Form-input-border);
    border-radius: 5px;
    border-radius: var(--Form-input-border-radius);
    font-size: 16px;
    font-size: var(--Form-input-font-size);
    height: 44px;
    line-height: 1;
    outline: none;      /* poor accessability */
    padding: 0 1rem;
    padding: var(--Form-input-padding);
    transition: .3s;
}

    /*  states
    ================================================================================================================= */
    .Form-input:hover,
    .Form-input:focus,
    .Form-input:focus-visible,
    .Form-input:focus-within,
    .Form-input:active {
        box-shadow: inset 0 0 0 2px #424E79;    /* TODO change hex */
        border-color: transparent;
        transition: .2s;
    }

    /*  error
    ================================================================================================================= */
    .Form-input.error {
        background: url(data:image/png;base64,) 95% 50% no-repeat;
        border-color:  rgb(255 0 0);
        border-color:  rgb(var(--Form-input-color-error));
    }
        .Form-input.error:hover {
            border-color: transparent;
        }

    /*  success
    ================================================================================================================= */
    .Form-input.success {
        background: url(data:image/png;base64,) 95% 50% no-repeat;
        border-color: rgb(0 255 0);
        border-color: rgb(var(--Form-input-color-success));
    }
        .Form-input.success:hover {
            border-color: transparent;
        }

    /*  search
    ================================================================================================================= */
    .Form-input.search {
        background: url(data:image/png;base64,) 95% 50% no-repeat;
    }

    /*  disabled
    ================================================================================================================= */
    .Form-input:disabled {
        background-color: #EFEFEF;
        border: 1px solid #AAB0C3;
        box-shadow: none;
        cursor: not-allowed;
    }

    /*  large
    ================================================================================================================= */
    .Form-input.large {
        font-size: 22px;
    }

