/*  title:  breadcrumbs
    notes:  component styles
 ==================================================================================================================== */




/*  variables
 ==================================================================================================================== */
:root {
    --Breadcrumbs-color: var(--Global-color-5, 114 123 155);
    --Breadcrumbs-color-active: 32 43 55;
}


/*  base
===================================================================================================================== */
.Breadcrumbs {
    border-bottom: 1px solid rgb(70 176 195);
    border-bottom: 1px solid rgb(var(--Global-border-color, 70 176 195));
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    padding: 1rem 2rem;
}

    /*  individual breadcrumb
    ================================================================================================================= */
    .Breadcrumbs .breadcrumb {
        color: rgb(114 123 155);
        color: rgb(var(--Breadcrumbs-color));
        cursor: pointer;
        font-weight: 600;
        font-size: 1rem;
        line-height: 100%;
        text-decoration: none;
        transition: .3s;
    }
        /*  states: active
        ============================================================================================================= */
        .Breadcrumbs .breadcrumb:hover,
        .Breadcrumbs .breadcrumb.active,
        .Breadcrumbs .breadcrumb:last-of-type{
            color: rgb(32 43 55);
            color: rgb(var(--Breadcrumbs-color-active));
            transition: .2s;
        }

        /*  >
        ============================================================================================================= */
        .Breadcrumbs .breadcrumb:not(:first-of-type)::before {
            color: rgb(70 50 251);
            color: rgb(var(--Global-color-1, 70 50 251));
            content: ">";
            margin: 0 .5rem;
        }

        /*  last "active"
        ============================================================================================================= */
        .Breadcrumbs .breadcrumb:last-of-type {
            cursor: auto;
            pointer-events: none;   /*  prevent a click to the same page.js (incase the slug present) */
        }

    /*  if a logo added
    ================================================================================================================= */
    .Breadcrumbs .logo {
        border: 1px solid 1px solid rgb(70 176 195);
        border: 1px solid 1px solid rgb(var(--Global-border-color, 70 176 195));
        border-radius: 2px;
        height: 22px;
        margin-right: .5rem;
        /*width: 16px;*/
    }
/*  title:  table
    notes:  component styles
 ==================================================================================================================== */




/*  variables
===================================================================================================================== */
:root {
    --Table-th-color: var(--Global-color-5, 114, 123, 155);
    --Table-td-color: var(--Global-color-5, 114, 123, 155);
    --Table-td-hover-color: var(--Global-color-1, 28 0 202);
}


/*  test
===================================================================================================================== */
.Table {
    border-collapse: collapse;
    padding: 0 2rem;
    width: 100%;
}
    .Table strong {
        color: rgb(32, 43, 55);
        font-weight: 700;
    }

    /*  head
    ================================================================================================================= */
    .Table th {
        border-bottom: 1px solid rgb(170 176 195);
        border-bottom: 1px solid rgb(var(--Global-border-color, 170 176 195));
        color: rba(114, 123, 155);
        color: rba(var(--Table-th-color));
        font-weight: 600;
        font-size: .625rem;
        line-height: .875rem;
        padding: 0 0 10px .5rem;
        text-align: left;
        letter-spacing: .08em;
        text-transform: uppercase;
    }

    /*  tr
    ================================================================================================================= */
    .Table tr {
        transition: .3s;
    }
        .Table tbody tr:hover {
            background-color: rgba(28 0 202, .1);
            background-color: rgba(var(--Table-td-hover-color), .1);
            cursor: pointer;
            transition: .2s;
        }

        /*  td
        ============================================================================================================= */
        .Table td {
            border-top: 1px solid #EFEFEF;
            color: rba(114, 123, 155);
            color: rba(var(--Table-td-color));
            font-family: Nunito Sans, sans-serif;
            font-size: .875rem;
            line-height: 1.4;
            padding: 1rem  .5rem;
        }

/*  title:  slideOut
    notes:  component styles
 ==================================================================================================================== */



/*  variables
 ==================================================================================================================== */
:root {
    --Slide-out-bg-color: rgb(241, 242, 245);
    --Slide-out-border-color: rgb(233, 233, 233);
}


/*  base
===================================================================================================================== */
.Slide-out {
    bottom: 0;
    left: 0;
    position: absolute;
    -webkit-transform: translateY(calc(100% - 20px));
            transform: translateY(calc(100% - 20px));
    transition: .3s;
    right: 0;
    z-index: 99;
}
    /*  animate the whole drawer to show the content    */
    .Slide-out.active {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        transition: .2s;
    }

    /*  toggle handle
    ================================================================================================================= */
    .Slide-out .toggle-handle {
        display: -webkit-flex;
        display: flex;
        left: 0;
        -webkit-justify-content: center;
                justify-content: center;
        position: absolute;
        top: 0;
        width: 100%;
    }
        /*  actual "tab" */
        .Slide-out .toggle-handle span {
            background-color: rgb(241, 242, 245);
            background-color: var(--Slide-out-bg-color);
            border-top: 1px solid rgb(233, 233, 233);
            border-top: 1px solid var(--Slide-out-border-color);
            border-radius:  5px;
            color: rgb(66 78 121);
            color: rgb(var(--Global-color-5-darker, 66 78 121));
            cursor: pointer;
            font-size: .625rem;
            font-weight: 700;
            padding: .5rem .75rem;
            text-transform: uppercase;
            -webkit-transform: translateY(-50%);
                    transform: translateY(-50%);
        }
            /*  active, rotate the icon    */
            .Slide-out .toggle-handle span.active .icon {
                -webkit-transform: rotate(0deg);
                        transform: rotate(0deg);
                transition: .1s;
            }

            /*  icon    */
            .Slide-out .toggle-handle .icon {
                margin-left: .25rem;
                -webkit-transform: rotate(180deg);
                        transform: rotate(180deg);
                transition: .1s;
            }

    /*  content
    ================================================================================================================= */
    .Slide-out .content {
        background-color: rgb(241, 242, 245);
        background-color: var(--Slide-out-bg-color);
        border: 1px solid rgb(233, 233, 233);
        border: 1px solid var(--Slide-out-border-color);
        border-radius: 5px;
        padding: 1.5rem;
    }

