/*  title:  titleBar
    notes:  molecule styles
 ==================================================================================================================== */




/*  variables
===================================================================================================================== */
:root {
    --Title-bar-break: var(--global-break-medium, 767px);
}


/*  basic - mobile first
===================================================================================================================== */
.Title-bar {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    /*padding: 2rem;*/
}
    .Title-bar h1,
    .Title-bar h2 {
        margin: 0;
        padding: 0;
        width: 100%;
    }




